<template>
  <div>
    <button @click="openFile">click</button>
    <label for="file">
      <input
        type="file"
        ref="fileInput"
        @change="handleFileUpload"
        accept="image/*"
        id="file"
        class="d-none"
        multiple
      />
    </label>
    <div id="imageContainer"></div>
  </div>
</template>

<script setup>
import { ref } from "vue";
const fileInput = ref(null);

function openFile() {
  fileInput.value.click();
}

function handleFileUpload(event) {
  const files = event.target.files;
  const imageContainer = document.getElementById("imageContainer");

  for (const file of files) {
    if (file.type.startsWith("image/")) {
      convertToJpegAndDisplay(file, imageContainer);
    }
  }
}

function convertToJpegAndDisplay(file, container) {
  const reader = new FileReader();

  reader.onload = function(event) {
    const img = new Image();
    img.src = event.target.result;

    img.onload = function() {
      const canvas = document.createElement("canvas");
      const ctx = canvas.getContext("2d");
      canvas.width = img.width;
      canvas.height = img.height;
      ctx.drawImage(img, 0, 0, img.width, img.height);

      // Convert the image to JPEG
      const jpegDataUrl = canvas.toDataURL("image/jpeg", 0.8);

      // Display the converted image
      const convertedImg = new Image();
      convertedImg.src = jpegDataUrl;
      container.appendChild(convertedImg);
    };
  };

  reader.readAsDataURL(file);
}
</script>
